
import { defineComponent, ref } from "vue";
import { useRouter } from "vue-router";
import { ElNotification } from "element-plus";

const Component = defineComponent({
  name: "FabricCardEditable",
  props: {
    data: {
      type: Object,
      default: () => ({})
    }
  },
  setup(_, context) {
    const router = useRouter();

    const onImageClick = () => {
      router.push({ name: "FabricDetail", params: { id: "id" } });
    };

    const onPlusClick = () => {
      context.emit("add-to-collection");
    };

    const onEditClick = () => {
      ElNotification({
        title: "Success",
        type: "success",
        customClass: "message",
        message: "Send to supervisor for approval"
      });
    };

    const onDeleteConfirm = () => {
      console.log("Delete");
    };

    return {
      onImageClick,
      onPlusClick,
      onEditClick,
      onDeleteConfirm
    };
  }
});

export default Component;
